import React, { useState } from 'react';
import PropTypes from 'prop-types';
import DSButton from '@elliemae/ds-basic/Button';
import { useFocusGroupWithState } from '@elliemae/ds-shared/FocusGroup';
import { Position } from '../Popper/positions';

const ToolbarItem = ({ icon, labelText, menu = undefined, ...otherProps }) => {
  const [ref, setRef] = useState(null);

  useFocusGroupWithState(ref);
  const btn = (
    <DSButton
      {...otherProps}
      buttonType="text"
      icon={icon}
      innerRef={(r) => setRef(r)}
      labelText={labelText}
    />
  );
  if (menu) {
    return React.cloneElement(menu, {
      ...menu.props,
      placement: Position.BOTTOM_END,
      triggerComponent: btn,
    });
  }
  return btn;
};

ToolbarItem.displayName = 'ToolbarItem';

ToolbarItem.propTypes = {
  icon: PropTypes.element,
  labelText: PropTypes.string,
};

export default ToolbarItem;
